@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans Bold";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans Light";
  src: url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../fonts/OpenSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: white;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.main {
  background-color: #FFEFD5;
}

.posts {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}

.posts__list {
  width: 100%;
  list-style-type: none;
  gap: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
}

.posts__element {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;
  background-color: #4682B4;
  border-radius: 12%;
  width: 300px;
  height: 250px;
}
.posts__element:hover {
  background-color: rgba(36, 133, 213, 0.8823529412);
  transition: 0.2s;
}

.button {
  background-color: #7B68EE;
  width: 50px;
  height: 35px;
  border-radius: 10%;
}
.button:hover {
  background-color: #9400D3;
  transition: 0.2s;
}
.button:active {
  background-color: #a82be6;
}

.button__prev {
  justify-content: flex-start;
}

.button__next {
  justify-content: flex-end;
}

.button__clip {
  width: 25px;
  height: 25px;
  background-image: url("/img/delete-svgrepo-com.svg");
  cursor: pointer;
}